import { mixins, Pie } from "vue-chartjs";
const { reactiveProp } = mixins;
export default {
  props: ["chartData", "gen"],
  data() {
    return {
      options: { cutoutPercentage: 20 },
    };
  },
  mounted() {
    this.renderChart(this.chartData, this.options);
  },
  watch: {
    gen() {
      this.renderChart(this.chartData, this.options);
    },
  },
  extends: Pie,
  mixins: { reactiveProp },
};
