<template>
  <div>
    <v-row>
      <v-col xs="12" md="6" sm="6" lg="6" xl="6">
        <time-chart :chartData="chartData" :gen="gen" />
      </v-col>
      <v-col xs="12" md="6" sm="6" lg="6" xl="6">
        <v-textarea outlined v-model="timeinfo" label=""></v-textarea>
        <!-- <v&#45;btn @click="updateChart">update</v&#45;btn> -->
        <v-btn @click="parseTime(true)">update</v-btn>
        <v-date-picker v-model="picker" show-current></v-date-picker>
      </v-col>
    </v-row>
  </div>
</template>
<script>
const sixtyArray = Array(24).fill(60);
import firebase from "firebase";
import TimeChart from "@/assets/js/time.js";
export default {
  components: { TimeChart },
  watch: {
    picker() {
      this.firebaseget();
    },
  },
  data() {
    return {
      label: "",
      color: {},
      timeinfo: "",
      chartData: {
        datasets: [
          {
            data: [10, 20, 30],
            backgroundColor: ["#de9610", "#e1d0b4", "#bd8683"],
          },
          {
            data: [10, 10],
            backgroundColor: ["#B71C1C", "#ffffff"],
          },
          {
            data: sixtyArray,
            backgroundColor: [
              "#dddddd",
              "#777777",
              "#dddddd",
              "#777777",
              "#dddddd",
              "#777777",
              "#dddddd",
              "#777777",
              "#ffffff",
              "#bbbbbb",
              "#ffffff",
              "#bbbbbb",
              "#ffffff",
              "#bbbbbb",
              "#ffffff",
              "#bbbbbb",
              "#ffffff",
              "#bbbbbb",
              "#ffffff",
              "#bbbbbb",
              "#ffffff",
              "#bbbbbb",
              "#ffffff",
              "#bbbbbb",
            ],
          },
        ],
        labels: [
          "(iPad)",
          "()",
          "()",
        ],
      },
      picker: new Date(Date.now() + 9 * 3600 * 1000)
        .toISOString()
        .substr(0, 10),
      gen: 0,
    };
  },
  mounted() {
    //this.timeinfo = localStorage.getItem("timeinfo") || "";
    this.firebaseget();
    // this.parseTime();
  },
  methods: {
    async firebaseget() {
      const db = firebase.firestore();
      const uid = firebase.auth().currentUser.uid;
      console.log(uid);
      const doc = db
        .collection("users")
        .doc(uid)
        .collection("time")
        .doc(this.picker);
      try {
        const retdoc = await doc.get();
        if (retdoc.exists) {
          const data = retdoc.data();
          console.log(data);
          this.timeinfo = data.timedata;
          this.parseTime(false);
        } else {
          console.log("no such document");
        }
      } catch (e) {
        console.error(e);
      }
    },
    firebaseput() {
      const db = firebase.firestore();
      const uid = this.$store.state.user.uid;
      console.log(uid);
      const doc = db
        .collection("users")
        .doc(uid)
        .collection("time")
        .doc(this.picker);
      doc.set({ timedata: this.timeinfo });
    },
    str2color(str) {
      let strint = [].reduce.call(
        str,
        (acc, cur) => {
          return acc + cur.charCodeAt(0);
        },
        0
      );
      return `#${((strint * 5801407) % 16777216).toString(16)}`;
    },
    updateChart() {
      this.chartData.datasets[0]["data"] = [10, 20, 40];
      this.gen++;
    },
    async parseTime(fbput) {
      if (this.timeinfo === "") {
        return;
      }
      localStorage.setItem("timeinfo", this.timeinfo);
      if (fbput) {
        this.firebaseput();
      }
      let timeinfoLns = this.timeinfo.split("\n");
      let timeinfoArrs = timeinfoLns.map((el) => {
        return el.split(", ");
      });
      const now = new Date();
      const year = now.getFullYear();
      const month = now.getMonth() + 1;
      const day = now.getDay();
      const hour = now.getHours();
      const min = now.getMinutes();
      const dayBeginTime = Date.UTC(year, month - 1, day, 0, 0, 0);
      const nowTime = Date.UTC(year, month - 1, day, hour, min, 0);
      const nowMin = (nowTime - dayBeginTime) / 60000;
      this.chartData.datasets[1].data = [nowMin, 1440 - nowMin];
      let timeArr2 = timeinfoArrs.map((el) => {
        let arr = new Array(3);
        let beginTime = el[0].split(":");
        let endTime = el[1].split(":");
        arr[0] =
          (Date.UTC(year, month - 1, day, beginTime[0], beginTime[1], 0) -
            dayBeginTime) /
          60000;
        arr[1] =
          (Date.UTC(year, month - 1, day, endTime[0], endTime[1], 0) -
            dayBeginTime) /
          60000;
        arr[2] = el[2];
        return arr;
      });
      let previousEnd = 0;
      let data = [];
      let color = [];
      let label = [];
      for (let ar of timeArr2) {
        if (ar[0] != previousEnd) {
          data.push(ar[0] - previousEnd);
          color.push("#d3d3d3");
          label.push("none");
        }
        data.push(ar[1] - ar[0]);
        // color.push(this.colorMap[ar[2]] || "#000000");
        color.push(this.str2color(ar[2]));
        label.push(ar[2]);
        previousEnd = ar[1];
      }
      if (previousEnd != 1440) {
        data.push(1440 - previousEnd);
        color.push("#d3d3d3");
        label.push("none");
      }
      this.chartData.datasets[0].data = data;
      this.chartData.datasets[0].backgroundColor = color;
      this.chartData.labels = label;
      this.gen++;
    },
  },
};
</script>
